import React from 'react';
import './ContentBlock.css';
import Banner from '../Banner/Banner';
import AboutMe from '../AboutMe/AboutMe';
import ContactMe from '../ContactMe/ContactMe';
import Portfolio from '../Portfolio/Portfolio';

import { Route, Routes } from 'react-router-dom';

class ContentBlock extends React.Component {
    constructor(props) {
        super(props);
        this.deactivateHomeMenuItem = this.deactivateHomeMenuItem.bind(this);
    }

    deactivateHomeMenuItem(menuItemValue) {
        const home = document.getElementById(menuItemValue)
        console.log('home: ', home);
        menuItemValue === 'home' ? home.removeAttribute('class') : console.log();
        console.log(home.getAttribute('class'));
}
    render() {
        console.log("public url:", process.env.PUBLIC_URL);
        console.log("env:", process.env);
        return (
            <main className="ContentBlock">
                <Routes>
                    <Route path='/' element={<Banner deactivateHomeMenuItem={this.props.deactivateHomeMenuItem} />} />
                    <Route path='/home' element={<Banner deactivateHomeMenuItem={this.props.deactivateHomeMenuItem} />} />
                </Routes>

                <Routes>
                    <Route path={process.env.PUBLIC_URL + '/'} element={<div className="separator"></div>} />
                    <Route path={process.env.PUBLIC_URL + '/home'} element={<div className="separator"></div>} />
                </Routes>

                <Routes>
                    <Route path={process.env.PUBLIC_URL + '/'} element={<AboutMe />} />
                    <Route path={process.env.PUBLIC_URL + '/home'} element={<AboutMe />} />
                </Routes>
                <Routes>
                    <Route path={process.env.PUBLIC_URL + '/contact-me'} element={<ContactMe deactivateHomeMenuItem={this.props.deactivateHomeMenuItem} />} />
                </Routes>
                <Routes>
                    <Route path={process.env.PUBLIC_URL + '/portfolio/*'} element={<Portfolio deactivateHomeMenuItem={this.props.deactivateHomeMenuItem} />} />
                </Routes>
            </main>
        );
    }
};

export default ContentBlock;